
































import { Vue, Component } from 'vue-property-decorator';
import CurrencyComponent from './components/CurrencyComponent.vue';
import { Currency } from './models/currency.model';

declare module 'vue/types/vue' {
  interface Vue {
    apiEndpoint: string;
    result?: Currency;
    loading: boolean;
    query: string;
    lastSearch: string;
    fetchData: Function;
  }
}

@Component({
  components: {
    "app-currency": CurrencyComponent
  },
  data() {
    return {
      apiEndpoint: "https://kns-crypto-search.herokuapp.com/coin?search=",
      result: null,
      loading: null,
      query: ""
    };
  },
  methods: {
    async fetchData(query: string) {
      if(!(this.result && this.result.status === "success" && query.toUpperCase() === this.result!.symbol.toUpperCase()) && query !== this.lastSearch) {
        this.lastSearch = query;
        this.loading = true;
        let res = await fetch(`${this.apiEndpoint}${query}`).then(re => re.json());
        if(res.status !== "error") {
          this.result = res;
        } else {
          this.result = undefined;
        }
        this.loading = false;
      }
    },
    search(e: Event) {
      e.preventDefault();
      this.fetchData(this.query);
    }
  },
  created() {
    this.fetchData("btc");
  }
})
export default class App extends Vue {}
